.navbar-donors {
  color: #cc0000;
  width: 100%;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  padding: 1.2rem 0rem;
  top: 0;
  left: 0;
  right: 0;
  height: 20px;
  font-size: 19px;
}

.navbar-banks {
  color: #cc0000;
  width: 100%;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  padding: 1.2rem 0rem;
  top: 0;
  left: 0;
  right: 0;
  height: 100px;
  font-size: 14px;
}

.dropdown {
  position: relative;
  font-size: 14px;
}

.dropdown-menu {
  display: none; /* Oculto por defecto */
  position: absolute;
  top: 100%; /* Coloca el menú justo debajo del botón */
  left: 0;
  background-color: white;
  padding: 0;
  margin: 0;
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.1);
  border-radius: 4px;
  z-index: 1; /* Asegura que el menú esté por encima de otros elementos */
}

.dropdown:hover .dropdown-menu {
  display: block; /* Muestra el menú al pasar el cursor */
}

.dropdown-menu li {
  list-style: none;
}

.btn-user-list {
  background: none;
  border: none;
  padding: 10px 20px;
  color: #cc0000;
  font: inherit;
  cursor: pointer;
  font-size: 12px;
  text-align: left;
  width: 100%;
}

.btn-user-list:hover {
  color: white;
  background-color: #cc0000;
  border-radius: 4px;
}
